<script>
export default {
  name: 'login',
  data: function () {
    var checkEmail = (rule, value, callback) => {
      const mailReg = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/
      if (!value) {
        return callback(new Error('邮箱不能为空'))
      }
      setTimeout(() => {
        if (mailReg.test(value)) {
          callback()
        } else {
          callback(new Error('请输入正确的邮箱格式'))
        }
      }, 100)
    }

    return {
      typeBtn: 0,
      ruleForm: {
        username: 'admin',
        password: '123123',
        Mail: '1815903837@qq.com'
      },
      rules: {
        Mail: [{ required: true, trigger: 'blur', validator: checkEmail }],

        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [{ required: true, message: '请输入密码', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (!valid) {
          this.$message.error('登录表单字段输入格式有误')
          return false
        }
        localStorage.setItem('username', this.ruleForm.username)
        this.$router.push('/')
      })
    }
  },
  mounted () {
    // 实现页面底部波纹特效
    const canvas = document.getElementById('canvas')
    const ctx = canvas.getContext('2d')
    canvas.width = canvas.parentNode.offsetWidth
    canvas.height = 500
    console.log(canvas.width, canvas.height)

    // 如果浏览器支持requestAnimFrame则使用requestAnimFrame否则使用setTimeout
    window.requestAnimFrame = (function () {
      return (
        window.requestAnimationFrame ||
        window.webkitRequestAnimationFrame ||
        window.mozRequestAnimationFrame ||
        window.oRequestAnimationFrame ||
        window.msRequestAnimationFrame ||
        function (callback) {
          window.setTimeout(callback, 1000 / 60)
        }
      )
    })()

    // 波浪大小
    const boHeight = 40 // canvas.height / 10;
    const posHeight = canvas.height - 150 // canvas.height / 1.2;

    // 初始角度为0
    let step = 0
    // 定义三条不同波浪的颜色
    const lines = [
      'rgba(22, 123, 255, 0.2)',
      'rgba(0, 165, 255, 0.6)',
      'rgba(0, 122, 255, 0.4)'
    ]

    function loop () {
      ctx.clearRect(0, 0, canvas.width, canvas.height)
      step++
      // 画3个不同颜色的矩形
      for (let j = lines.length - 1; j >= 0; j--) {
        ctx.fillStyle = lines[j]

        // 每个矩形的角度都不同，每个之间相差45度
        const angle = ((step + j * 70) * Math.PI) / 180 // 50
        const deltaHeight = Math.sin(angle) * boHeight
        const deltaHeightRight = Math.cos(angle) * boHeight
        ctx.beginPath()
        ctx.moveTo(0, posHeight + deltaHeight)
        ctx.moveTo(0, posHeight + deltaHeight)
        ctx.bezierCurveTo(
          canvas.width / 2,
          posHeight + deltaHeight - boHeight,
          canvas.width / 2,
          posHeight + deltaHeightRight - boHeight,
          canvas.width,
          posHeight + deltaHeightRight
        )
        ctx.lineTo(canvas.width, canvas.height)
        ctx.lineTo(0, canvas.height)
        ctx.lineTo(0, posHeight + deltaHeight)
        ctx.closePath()
        ctx.fill()
      }
      window.requestAnimFrame(loop)
    }
    loop()
  }
}
</script>

